import React, { useEffect, useState, useRef } from 'react';
import { Client, Stomp } from '@stomp/stompjs';
import logo from './commons/images/icon.png';
import bellIcon from './commons/images/bell-icon.png'; // Bell icon for notifications
import chatIcon from './commons/images/chat-icon.png'; // Bell icon for notifications
import userIcon from './commons/images/user-icon.png';
import { jwtDecode } from 'jwt-decode';
import {
    DropdownItem,
    DropdownMenu,
    DropdownToggle,
    Nav,
    Navbar,
    NavbarBrand,
    NavLink,
    UncontrolledDropdown,
    Button,
    Badge} from 'reactstrap';
import { withRouter } from 'react-router-dom';
import './commons/styles/project-style.module.css';
import styles from 'react-day-picker/style.css';
import style from './commons/styles/project-style.module.css';
import SockJS from "sockjs-client";
//import Stomp from "stompjs";

const textStyle = {
    color: 'white',
    textDecoration: 'none'
};

const NavigationBar = (props) => {
    const [isLoggedIn, setIsLoggedIn] = useState(false);
    const [userName, setUserName] = useState('');
    const [isAdmin, setIsAdmin] = useState(false);
    const [notifications, setNotifications] = useState([]);
    const [messages, setMessages] = useState([]);
    const [inputMessage, setInputMessage] = useState("");

    const personId = sessionStorage.getItem('personId'); // Retrieve person ID from session storage
    const websocketUrl = 'http://localhost:80/api-monitor/ws';

    useEffect(() => {
        checkLoginStatus();
        if (isLoggedIn && personId) {
            setupWebSocketConnection(personId);
        }
    }, [isLoggedIn, personId]);

    const checkLoginStatus = () => {
        const token = sessionStorage.getItem('jwt');
        if (token) {
            setIsLoggedIn(true);
            const decodedToken = jwtDecode(token);
            setUserName(decodedToken.username || 'User');
            setIsAdmin(decodedToken.admin);
        }
    };

    const setupWebSocketConnection = (personId) => {
        const client = new Client({
            brokerURL: websocketUrl,
            reconnectDelay: 5000,
            debug: (str) => console.log(str),
        });

        client.onConnect = () => {
            console.log(`Connected to WebSocket for person ID: ${personId}`);

            // Subscribe to a unique topic for each personId
            client.subscribe(`/topic/alert/${personId}`, (message) => {
                const messageData = message.body;
                setNotifications((prevNotifications) => [...prevNotifications, messageData]);
            });
        };

        client.onStompError = (frame) => {
            console.error(`STOMP error for person ID ${personId}:`, frame);
        };

        client.activate();

        return () => {
            client.deactivate();
        };
    };

    const useWebSocket = (personId, onMessageReceived) => {
        const stompClient = useRef(null);
    
        useEffect(() => {
            // Connect to the WebSocket endpoint
            const socket = new SockJS("http://localhost:80/api-chat/ws");
            stompClient.current = Stomp.over(socket);
    
            stompClient.current.connect({}, (frame) => {
                console.log("Connected: " + frame);
    
                // Subscribe to the user-specific topic
                stompClient.current.subscribe(`/topic/chat/${personId}`, (message) => {
                    onMessageReceived(JSON.parse(message.body));
                    console.log("mesaj primit ", message.body);
                });
            });
    
            return () => {
                if (stompClient.current) {
                    stompClient.current.disconnect();
                }
            };
        }, [personId, onMessageReceived]);
    
        // Function to send a message
        const sendMessage = (message) => {
            if (stompClient.current && stompClient.current.connected) {
                stompClient.current.send(
                    `/app/sendMessage/${personId}`,
                    {},
                    JSON.stringify(message)
                );
            }
        };
    
        return { sendMessage };
    };



    // Define a callback to handle incoming messages
    const handleIncomingMessage = (newMessage) => {
        setMessages((prevMessages) => [...prevMessages, newMessage]);
    };

    // Use the custom hook
    const { sendMessage } = useWebSocket(personId, handleIncomingMessage);

    const handleSendMessage = () => {
        if (inputMessage.trim()) {
            const message = {
                senderId: personId,
                content: inputMessage,
                timestamp: new Date().toISOString(),
            };

            sendMessage(message); // Call the sendMessage function from the hook
            setInputMessage(""); // Clear the input field after sending
        }
    }
    

    const navigateToLogin = () => {
        props.history.push('/auth/login');
        checkLoginStatus();
    };

    const logOff = () => {
        sessionStorage.removeItem('jwt');
        sessionStorage.removeItem('role');
        sessionStorage.removeItem('personId');
        setIsLoggedIn(false);
        setUserName('');
        setIsAdmin(false);
        setNotifications([]);
        setMessages([]);
        props.history.push('/');
    };

    // Function to remove notification by index
    const removeNotification = (index) => {
        setNotifications((prevNotifications) => prevNotifications.filter((_, i) => i !== index));
    };

    const removeChat = (index) => {
        setMessages((prevChat) => prevChat.filter((_, i) => i !== index));
    };

    return (
        <div>
            <Navbar color="dark" light expand="md">
                <NavbarBrand href="/">
                    <img src={logo} width={"35"} height={"35"} alt="Logo" className={style.imageLogo} />
                </NavbarBrand>
                <Nav className="navbar-nav mr-auto" navbar>
                    <UncontrolledDropdown nav inNavbar className={styles.dropdown}>
                        <DropdownToggle style={textStyle} nav caret>
                            Menu
                        </DropdownToggle>
                        <DropdownMenu left>
                            {isAdmin ? (
                                <>
                                    <DropdownItem>
                                        <NavLink href="/person">Persons</NavLink>
                                    </DropdownItem>
                                    <DropdownItem>
                                        <NavLink href="/device">Devices</NavLink>
                                    </DropdownItem>
                                </>
                            ) : (
                                <>
                                    <DropdownItem>
                                        <NavLink href="/myDevice">My Devices</NavLink>
                                    </DropdownItem>
                                    <DropdownItem>
                                        <NavLink href="/ws">Statistics(WS)</NavLink>
                                    </DropdownItem>
                                </>
                            )}
                        </DropdownMenu>
                    </UncontrolledDropdown>
                </Nav>
                <Nav className="navbar-nav">
                    {/* Notification Bell Icon */}
                    <UncontrolledDropdown nav inNavbar>
                        <DropdownToggle nav caret style={{ color: 'white' }}>
                            <img
                                src={chatIcon}
                                width="30"
                                height="30"
                                alt="Chat"
                                style={{ marginRight: '10px' }}
                            />
                            {messages.length > 0 && (
                                <Badge color="danger" pill>{messages.length}</Badge>
                            )}
                        </DropdownToggle>
                        <DropdownMenu right>
                            {messages.length > 0 ? (
                                messages.map((notif, index) => (
                                    <DropdownItem key={index} style={{ display: 'flex', justifyContent: 'space-between', alignItems: 'center' }}>
                                        <span>{notif}</span>
                                        <Button
                                            className={style.notifDeleteButton}
                                            onClick={() => removeChat(index)}
                                            
                                        >
                                            X
                                        </Button>
                                    </DropdownItem>
                                ))
                            ) : (
                                <DropdownItem>No new chats</DropdownItem>
                            )}
                        </DropdownMenu>
                    </UncontrolledDropdown>

                    <UncontrolledDropdown nav inNavbar>
                        <DropdownToggle nav caret style={{ color: 'white' }}>
                            <img
                                src={bellIcon}
                                width="30"
                                height="30"
                                alt="Notifications"
                                style={{ marginRight: '10px' }}
                            />
                            {notifications.length > 0 && (
                                <Badge color="danger" pill>{notifications.length}</Badge>
                            )}
                        </DropdownToggle>
                        <DropdownMenu right>
                            {notifications.length > 0 ? (
                                notifications.map((notif, index) => (
                                    <DropdownItem key={index} style={{ display: 'flex', justifyContent: 'space-between', alignItems: 'center' }}>
                                        <span>{notif}</span>
                                        <Button
                                            className={style.notifDeleteButton}
                                            onClick={() => removeNotification(index)}
                                            
                                        >
                                            X
                                        </Button>
                                    </DropdownItem>
                                ))
                            ) : (
                                <DropdownItem>No new notifications</DropdownItem>
                            )}
                        </DropdownMenu>
                    </UncontrolledDropdown>

                    {isLoggedIn ? (
                        <UncontrolledDropdown nav inNavbar>
                            <DropdownToggle nav caret style={{ display: 'flex', alignItems: 'center' }}>
                                <img
                                    src={userIcon}
                                    width="30"
                                    height="30"
                                    alt="User Icon"
                                    style={{ marginRight: '10px' }}
                                />
                                <span style={{ color: 'white' }}>
                                    {userName} {isAdmin ? "(Admin)" : "(Client)"}
                                </span>
                            </DropdownToggle>
                            <DropdownMenu right>
                                <DropdownItem onClick={logOff}>
                                    Log out
                                </DropdownItem>
                            </DropdownMenu>
                        </UncontrolledDropdown>
                    ) : (
                        <Button color="secondary" onClick={navigateToLogin} style={{ marginLeft: '10px' }}>
                            Log In
                        </Button>
                    )}
                </Nav>
            </Navbar>
        </div>
    );
};

export default withRouter(NavigationBar);
